.flex {
  display: flex;
}

body {
  color: $color-text;
}
.container {
  max-width: 1200px !important;

}


.footer {
  position: relative;
  background-color: $color-dark-blue;
  padding-top: 95px;
  padding-bottom: 45px;

  @include text-sm;
  line-height: 1.2;
  color: $color-grey-blue;

  a {
    color: inherit;
  }


  &_bg {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    object-fit: cover;
  }

  &__item {
    margin-bottom: 15px;
  }

  &__email {
    color: $color-blue;
    transition: $ease-main;

    &:hover {
      opacity: .8;
      color: $color-blue;
    }
  }

  .icon {
    margin-right: 5px;
  }

  .phone {
    @include text-lg;

    display: block;
    color: #fff;
    margin-bottom: 10px;
  }
  &__payment {
    p {
      margin-bottom: 10px;
    }
  }
  &__icons {
    margin-bottom: 20px;
  }

  &__list {
    padding-left: 0px;
  }
  &__col {
    padding-left: 15px;
    padding-right: 15px;
  }
  .row {
    justify-content: space-between;
  }

}

.header {

}

.icon {
  display: block;
}
