// Текстовые стили, используемые на сайте
@mixin logo-text {
  font-family: $font-main;
  font-size: 43px;
  letter-spacing: -1px;
  font-weight: 900;

  @include screen('tablet', max) {
    font-size: 52px;
    line-height: 1.34;
  }

  @include screen('phone', max) {
    font-size: 23px;
    line-height: 1.35;
  }
}

@mixin heading-1 {
  font-family: $font-main;
  font-size: 72px;
  font-weight: 900;
}

@mixin heading-2 {
  font-family: $font-main;
  font-size: 30px;
  font-weight: 700;
}

@mixin heading-3 {
  font-size: 24px;
  font-weight: 700;
}

// TODO: start



@mixin heading-6 {
  font-size: 16px;
  line-height: 1.6;
  font-weight: 400;
}
// TODO: end;

@mixin text {
  font-family: $font-main;
  font-size: 15px;
  font-weight: 400;
  //
  // @include screen('tablet', max) {
  //   font-size: 16px;
  //   line-height: 1.5;
  // }
}
@mixin text-huge {
  font-family: $font-main;
  font-size: 20px;
  font-weight: 400;
}

@mixin text-lg {
  font-family: $font-main;
  font-size: 18px;
  font-weight: 400;
}

@mixin text-md {
  font-family: $font-main;
  font-size: 16px;
  font-weight: 400;
}

@mixin text-sm {
  font-family: $font-main;
  font-size: 13px;
  font-weight: 400;
}








@mixin text-small-semibold {
  font-family: $font-main;
  font-size: 16px;
  line-height: 1.5;
  font-weight: 600;
}






@mixin text-small-bold {
  font-family: $font-main;
  font-size: 16px;
  line-height: 1.5;
  font-weight: 500;
}

@mixin text-md {
  font-family: $font-main;
  font-size: 24px;
  line-height: 1;
  font-weight: 600;
}

@mixin text-xs {
  font-family: $font-main;
  font-size: 14px;
  line-height: 1.71;
  font-weight: 600;
}

@mixin text-xs-normal {
  font-family: $font-main;
  font-size: 14px;
  line-height: 1.5;
  font-weight: 400;
}

@mixin text-xxs {
  font-family: $font-main;
  font-size: 10px;
  font-weight: 600;
}
